export default {
  name: 'Mine',
  components: {

  },
  data () {
    return {
      userInfo: {
        nickName: '',
        headImage: '',
        balance: 0,
        withDrawCash: 0
      }
    }
  },
  created () {
    this.getData()
  },
  methods: {
    getData () {
      this.$http.fetchGet('/merchant/center/get/main', {merchantId: this.$store.state.merchantId}).then((res) => {
        this.userInfo = res.data.data
      })
    },
    myMoney(){
      this.$router.push({
        name: 'myMoney'
      })
    },
    certification () {
      this.$router.push({
        name: 'certification'
      })
    },
    goSetting () {
      this.$router.push({
        name: 'Setting'
      })
    }
  }
}



